	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Усложнений вариант выполнения классического упражнения отжимания в петлях TRX. В отличии тяги одной рукой может вызвать затруднения у новичков. Так как необходимо в достаточной мере владеть своим телом. А также уметь равномерно распределять свой вес на точки опоры.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Выровняйте петли, чтоб оба конца были ровны.</li>\n  <li>Спутайте рукоятки таким образом, чтобы можно было взяться за одну, без перекоса всего тренажера.</li>\n  <li>Возьмитесь за рукоять, пройдите вперед до полного натяжения лямок.</li>\n  <li>Распределите свой вес таким образом, чтобы не было перекоса тела.</li>\n  <li>Медленно сгибая руку в локте, опуститесь вниз как при отжиманиях от пола. Опускайтесь до тех пор, пока угол в локтевом суставе составит 90 градусов.</li>\n  <li>Ладонь не меняет своего положения (положение ладони - обращены к себе)</li>\n  <li>Поднимаемся вверх до полного выпрямления руки.</li>\n  <li>Так повторять заданное количество повторений</li>\n  <li>Меняем руку</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Если у вас не получается держать корпус без перекосов, то приступать к выполнению этого упражнения пока рано</li>\n  <li>Не опускайтесь вниз резко, соблюдайте плавность движений - это позволит вам избежать травм.</li>\n  <li>Поставьте ноги шире для устойчивости</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/z9Yyw0cYnPQ\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/otjimania-odnoi-rukoi-v-petlyah","efficiency":"5","title":"Отжимания одной рукой в петлях","image":"/img/0-13-0.png","image_2":"","image_3":"","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"3","bodypart":"Грудь","filtertype":"base","type":"Базовое","filterinventory":"trx","inventory":"TRX Петли","filterplace":""}}},"pathContext":{}}

